h2.no-background {
    position: relative;
    overflow: hidden;
    
    span {
        display: inline-block;
        vertical-align: baseline;
        zoom: 1;
        *display: inline;
        *vertical-align: auto;
        position: relative;
        padding: 0 20px;
        background-color: transparent;

        &:before, &:after {
            content: '';
            display: block;
            width: 1000px;
            position: absolute;
            top: 0.73em;
            border-top: 1px solid #3f51b5;
        }

        &:before { right: 100%; }
        &:after { left: 100%; }
    }
}


.decoration > div{
    background-color: #3f51b5;
